import { NotFoundError } from './../common/not-foud-error';
import { AppError } from './../common/app-error';
import { HttpService } from './../services/http.service';
import { Http } from '@angular/http';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-slider-control',
  templateUrl: './slider-control.component.html',
  styleUrls: ['./slider-control.component.css']
})
export class SliderControlComponent implements OnInit {

  private service;
  private chains = [];
 constructor(http: Http) { 
    
    this.service = new HttpService("http://127.0.0.1:8081/singlejoint",http);
  }

  ngOnInit() {

    this.service.get("http://127.0.0.1:8081/chains")
    .subscribe(
      response => {
         for (let o of response["Chains"]){
           let p =o["Val"];
            for (let u of p){
              this.chains.push({ Name :u["Name"] , Id: u["ID"] });
            }
        }
      },
      (error: AppError) => {

        //rimovo dal vettore
        
        if (error instanceof NotFoundError){
          //expected error
          //deleted
          //this.form.setErrors(error.json());
        }
        else{
          //unexpected error
          throw error;

        }
        
       });
  }

  sendVal(param: number, id:number){

    //{"joint":[{"id": 15, "val": 0},{"id": 16, "val": 0}]}
    this.service.create({"joint":[{"id": Number(id), "val": Number(param)}]})
    .subscribe(
      response => {
        
      },
      (error: AppError) => {

        //rimovo dal vettore
        
        if (error instanceof NotFoundError){
          //expected error
          //deleted
          //this.form.setErrors(error.json());
        }
        else{
          //unexpected error
          throw error;

        }
        
       });

  }

  onMaster(){

     this.service.get("http://127.0.0.1:8081/master?web")
    .subscribe(
      response => {
        
      },
      (error: AppError) => {

        //rimovo dal vettore
        
        if (error instanceof NotFoundError){
          //expected error
          //deleted
          //this.form.setErrors(error.json());
        }
        else{
          //unexpected error
          throw error;

        }
        
       });


  }
}



